"use client";

import { useRef } from "react";
import styles from "./EventSlider.module.css";
import { useRouter } from "next/navigation";

import {
  DATEICON,
  LOCATION_ICON,
  MODE_ICON,
  SAVEICON,
  TICKET_ICON,
  VIEW_ICON,
} from "@/const-value/config-icons/page";

export default function EventSlider({ title, data = [] }) {
  const router = useRouter();
  const sliderRef = useRef(null);

  const slideLeft = () => {
    sliderRef.current?.scrollBy({ left: -350, behavior: "smooth" });
  };

  const slideRight = () => {
    sliderRef.current?.scrollBy({ left: 350, behavior: "smooth" });
  };

  return (
    <section className={styles.eventSection}>
      {/* Header */}
      <div className={styles.eventHeader}>
        <h3>{title}</h3>
        <button className={styles.seeAllBtn}>See all</button>
      </div>

      <div className={styles.eventLine}></div>

      {/* Filters + Navigation */}
      <div className={styles.filterSection}>
        <div className={styles.filterRow}>
          <button className={`${styles.chip} ${styles.active}`}>All</button>
          <button className={styles.chip}>Education</button>
          <button className={styles.chip}>Sports</button>
          <button className={styles.chip}>Entertainment</button>
          <button className={styles.chip}>Networking</button>
        </div>

        <div className={styles.sliderBtns}>
          <button className={styles.navBtn} onClick={slideLeft}>❮</button>
          <button className={styles.navBtn} onClick={slideRight}>❯</button>
        </div>
      </div>

      {/* Cards */}
      <div className={styles.eventCards} ref={sliderRef}>
        {data.map((event, i) => (
          <div
            key={i}
            className={styles.eventCard}
            onClick={() => router.push(`/events/${event.identity}`)}
          >
            {event.tag && <span className={styles.tag}>{event.tag}</span>}

            <img
              src={event.bannerImage}
              alt={event.title}
              className={styles.eventImg}
            />

            <div className={styles.eventTitle}>
              <span>{event.title}</span>
              <span>{SAVEICON}</span>
            </div>

            <div className={styles.eventDetails}>
              <div>
                <p>{LOCATION_ICON} {event.venue || "N/A"}</p>
                <p>{TICKET_ICON} $200</p>
              </div>
              <div>
                <p>{DATEICON} {event.eventDate} {event.eventTime}</p>
                <p>{MODE_ICON} {event.mode}</p>
              </div>
            </div>

            <div className={styles.eventCardBottom}>
              <div className={styles.viewIcons}>{VIEW_ICON} 456</div>
              <div className={styles.priceBtn}>{event.type || "Paid"}</div>
            </div>
          </div>
        ))}
      </div>
    </section>
  );
}
